.card2-container {

    width: 250px;
    height: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px auto;
    box-shadow: 0px 0px 56px -8px rgba(0, 0, 0, 0.17);


}

.hexagon {
    height: 100px;
    /* adjust to control the size  */
    aspect-ratio: 1/cos(30deg);
    clip-path: polygon(50% -50%, 100% 50%, 50% 150%, 0 50%);
    background: #3B8686;
    position: relative;
    top: -13%;
    rotate: 30deg;
    display: flex;
    justify-content: center;
    align-items: center;




}

.card2-img {
    width: 50px;
    height: 50px;
    rotate: -30deg;
}

.card2-p1 {
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: "Quicksand", Arial, sans-serif;
    margin-top: -10px;
}

.card2-p2 {

    font-weight: 400;
    text-transform: uppercase;
    font-family: "Quicksand", Arial, sans-serif;

    margin: 0px 10px 0px 10px;
    text-align: center;

    font-size: 15px;
    line-height: 1.8;

}

@media (max-width:1400px) {

    .card2-container {

        width: 200px;
        height: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 30px auto;
        box-shadow: 0px 0px 56px -8px rgba(0, 0, 0, 0.17);


    }

    .hexagon {
        height: 70px;
        /* adjust to control the size  */
        aspect-ratio: 1/cos(30deg);
        clip-path: polygon(50% -50%, 100% 50%, 50% 150%, 0 50%);
        background: #3B8686;
        position: relative;
        top: -17%;
        rotate: 30deg;
        display: flex;
        justify-content: center;
        align-items: center;




    }

    .card2-img {
        width: 40px;
        height: 40px;
        rotate: -30deg;
    }

    .card2-p1 {
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        font-family: "Quicksand", Arial, sans-serif;
        margin-top: -10px;
    }

    .card2-p2 {

        font-weight: 400;
        text-transform: uppercase;
        font-family: "Quicksand", Arial, sans-serif;

        margin: 0px 10px 0px 10px;
        text-align: center;

        font-size: 12px;
        line-height: 1.8;

    }

}